import "./App.css";
import React from "react";
import { useState } from "react";

function App() {

  const [value,setValue] = useState([])
  const [result,setResult] = useState()
  const displayValue  = (num) => {
      setValue((prev)=> prev + num);
  }
  const reset = () => {
    setResult('')
    setValue('')
  }

  return (
    <div className="container">
      <div className="calculator">


        
        <div className="input-section">
          <input value={value} className="input" type="text" />
        </div>
        <div className="result-section">
          <input value={result} className="input" type="text" />
        </div>


        
        <div className="input-btns">
          <div className="btn">
            <button><i style={{fontSize:'23px',fontFamily: 'Anek Latin'}} class="fa-solid fa-percent"></i></button>
          </div>
          <div className="btn">
            <button style={{fontSize:'23px',fontFamily: 'Anek Latin'}}>CE</button>
          </div>
          <div className="btn">
            <button
            onClick={reset}
            style={{fontSize:'23px',fontFamily: 'Anek Latin'}}
            >AC</button>
          </div>
          <div className="btn">
            <button onClick={()=>{
              displayValue('/')
            }}><i style={{fontSize:'23px'}} class="fa-solid fa-divide"></i></button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}} onClick={()=>{
              displayValue('7')
            }}>7</button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}} onClick={()=>{
              displayValue('8')
            }}>8</button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}} onClick={()=>{
              displayValue('9')
            }}>9</button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}} onClick={()=>{
              displayValue('*')
            }}><i style={{fontSize:'23px'}} class="fa-solid fa-xmark"></i></button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}} onClick={()=>{
              displayValue('4')
            }}>4</button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}} onClick={()=>{
              displayValue('5')
            }}>5</button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}} onClick={()=>{
              displayValue('6')
            }}>6</button>
          </div>
          <div className="btn">
            <button onClick={()=>{
              displayValue('-')
            }}><i style={{fontSize:'23px'}} class="fa-solid fa-minus"></i></button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}}
            onClick={()=>{
              displayValue('1')
            }}
            >1</button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}}
            onClick={()=>{
              displayValue('2')
            }}
            >2</button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}}
            onClick={()=>{
              displayValue('3')
            }}
            >3</button>
          </div>
          <div className="btn">
            <button className="fun-btn" onClick={()=>{
              displayValue('+')
            }}><i style={{fontSize:'23px'}} class="fa-solid fa-plus"></i></button>
          </div>
          <div className="btn">
            <button></button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}} onClick={()=>{
              displayValue('0')
            }}>0</button>
          </div>
          <div className="btn">
            <button style={{fontFamily: 'Anek Latin'}} onClick={()=>{
              displayValue('.')
            }}>.</button>
          </div>
          <div className="equal-btn">
            <button
            className="fun-btn"
            onClick={()=>{
              
              setResult(eval(value))

            }}
            ><i style={{fontSize:'23px'}} class="fa-solid fa-equals"></i></button>
          </div>
          
          
        </div>
      </div>
      
      

    </div>



  );
}

export default App;
